.modal-ingredient {
  position: absolute;
  top: 25%;
  left: 50%;
  transform: translateX(-50%);
  margin: 0 auto;
  padding: 40px 20px;
  width: 100%;

  font-size: 16px;

  background-color: $extraDecorColor;

  @media screen and (min-width: $mobile) {
    width: 320px;
  }

  @media screen and (min-width: $tablet) {
    padding: 40px 28px 28px;
    width: 715px;
  }
}

.is-active {
  display: block;
}

.close-btn {
  position: absolute;
  top: 16px;
  right: 16px;
  width: 32px;
  height: 32px;
  outline: none;

  &:hover {
    fill: $hoverColor;
  }

  @media screen and (min-width: $tablet) {
    top: 12px;
    right: 12px;
  }
}

.ingredient__main-content {
  margin-bottom: 20px;

  @media screen and (min-width: $tablet) {
    padding: 0;
    display: flex;
    flex-direction: column;
    border-bottom: 1px solid $accentColor;
    padding-bottom: 40px;
    margin-bottom: 28px;
  }
}

.ingredient__title {
  margin-bottom: 20px;

  font-family: 'Roboto';
  font-weight: 900;
  font-size: 32px;
  line-height: calc(38 / 32);
  color: inherit;

  @media screen and (min-width: $tablet) {
    margin-bottom: 28px;
  }
}

.ingredient__type {
  font-weight: 600;
  line-height: calc(26 / 16);
  color: inherit;
  text-transform: uppercase;
}

.ingredient__name-accent {
  font-weight: 600;
  color: var(--heading-color);
}

.ingredient__desc {
  height: 112px;
  overflow-y: scroll;

  line-height: calc(28 / 16);
  margin-bottom: 28px;
  color: $textClGrayForLight;

  &::-webkit-scrollbar-track {
    background-color: transparent;
    border-radius: 2px;
  }

  &::-webkit-scrollbar {
    width: 6px;
    background-color: transparent;
  }

  &::-webkit-scrollbar-thumb {
    background-color: $accentColor;
    border-radius: 5px;
  }

  &::-webkit-scrollbar-thumb:hover {
    background: $hoverColor;
  }
}

.ingredient__info {
  padding-left: 10px;
  margin-bottom: 32px;
  line-height: calc(26 / 16);

  @media screen and (min-width: $tablet) {
    margin-bottom: 40px;
  }
}

.cocktail__button {
  display: block;
  padding: 12px 24px;
  font-family: 'Roboto';
  font-size: 20px;
  line-height: 24px;
  color: $extraDecorColor;
  background-color: $btnAccentBgColor;
  border-radius: 4px;

  @media screen and (min-width: $tablet) {
    margin-left: auto;
  }
}

.ingredient__info > li::marker {
  content: '✶';
  color: $textColorDark;
}
