.modal {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  margin: 0 auto;
  padding: 40px 20px;
  width: 100%;
  font-size: 16px;
  color: inherit;
  background-color: var(--bg-color);

  @media screen and (min-width: $mobile) and (max-width: $to-tablet) {
    height: 70vh;
    overflow: auto;
  }

  @media screen and (min-width: $mobile) {
    width: 320px;
  }

  @media screen and (min-width: $tablet) {
    padding: 40px 28px 28px;
    width: 90vw;
  }

  @media screen and (min-width: $desktop) {
    padding: 40px 28px 28px;
    width: 822px;
  }
}

.close-btn {
  position: absolute;
  top: 16px;
  right: 16px;
  width: 32px;
  height: 32px;
  outline: none;

  &:hover {
    fill: $hoverColor;
  }

  @media screen and (min-width: $tablet) {
    top: 12px;
    right: 12px;
  }
}

.cocktail__title {
  margin-bottom: 20px;
  font-family: 'Roboto';
  font-weight: 900;
  font-size: 32px;
  line-height: calc(38 / 32);
  color: inherit;

  @media screen and (min-width: $tablet) {
    margin: 0 0 28px 292px;
    order: 1;
  }
}

.cocktail__main-content {
  margin-bottom: 32px;

  @media screen and (min-width: $tablet) {
    padding: 0;
    display: flex;
    flex-direction: column;
    margin-bottom: 40px;
  }
}

.cocktail__desc-wrapper {
  margin-bottom: 32px;

  @media screen and (min-width: $tablet) {
    order: 3;
    margin: 0;
    width: 615px;
  }
}

.cocktail__instruction {
  margin-bottom: 20px;

  font-weight: 600;
  line-height: calc(26 / 16);
  color: var(--bg-heading);
  text-transform: uppercase;

  @media screen and (min-width: $tablet) {
    margin-bottom: 24px;
  }

  @media screen and (min-width: $desktop) {
    color: var(--bg-heading);
  }
}

.cocktail__desc {
  overflow-y: scroll;

  line-height: calc(28 / 16);
  color: $textClGrayForDark;

  &::-webkit-scrollbar-track {
    background-color: transparent;
    border-radius: 2px;
  }

  &::-webkit-scrollbar {
    width: 6px;
    background-color: transparent;
  }

  &::-webkit-scrollbar-thumb {
    background-color: $accentColor;
    border-radius: 5px;
  }

  &::-webkit-scrollbar-thumb:hover {
    background: $hoverColor;
  }
}

.cocktail__img-wrapper {
  margin-bottom: 22px;
  width: 100%;
  border-radius: 4px;
  overflow: hidden;

  @media screen and (min-width: $mobile) {
    width: 280px;
    height: 280px;
  }

  @media screen and (min-width: $tablet) {
    position: absolute;
    left: 0;
    top: 0;
    width: 288px;
    height: 320px;
    margin: 0;
  }
}

.cocktail__img {
  width: 100%;
  height: 100%;
}

.cocktail__info-block {
  @media screen and (min-width: $tablet) {
    order: 2;
    margin-left: 292px;
    margin-bottom: 52px;
  }
}

.cocktail__info-text {
  margin-bottom: 8px;
  font-weight: 600;
  line-height: calc(24 / 16);
  color: inherit;
  text-transform: uppercase;

  @media screen and (min-width: $desktop) {
    color: inherit;
  }
}

.cocktail__info-amount {
  margin-bottom: 8px;
  color: $textClGrayForDark;
  font-weight: 300;
  font-size: 14px;
  line-height: calc(21 / 14);
}

.cocktail__title-tablet {
  display: none;
}

.cocktail__ingredients {
  height: 130px;
  overflow-y: scroll;

  padding-left: 13px;
  line-height: calc(26 / 16);

  &::-webkit-scrollbar-track {
    background-color: transparent;
    border-radius: 2px;
  }

  &::-webkit-scrollbar {
    width: 6px;
    background-color: transparent;
  }

  &::-webkit-scrollbar-thumb {
    background-color: $accentColor;
    border-radius: 5px;
  }

  &::-webkit-scrollbar-thumb:hover {
    background: $hoverColor;
  }
}

.cocktail__button {
  display: block;
  padding: 12px 24px;
  font-family: 'Roboto';
  font-size: 20px;
  line-height: 24px;
  color: $extraDecorColor;
  background-color: $btnAccentBgColor;
  border-radius: 4px;
  transition: background-color linear 850ms;

  @media screen and (min-width: $tablet) {
    margin-left: auto;
  }

  &:hover {
    background-color: $btnBgColor;
  }
}

.cocktail__ingredients > li::marker {
  content: '✶';
  color: var(--bg-heading);
}

.cocktail__ingredients li:hover {
  color: $textColorAccent;
  cursor: pointer;
}
